main{
  display: flex;
}

body {
  background-image: url('./desk-planner2.jpeg');
  background-size: 100%;
  background-position: absolute;
}

.title {
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 2%;
  /* flex-direction: row-reverse; */
}

/* Nav links styling */
a {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
  color: white;
}

nav {
  background-color: rgb(59, 56, 56);
}

/* Add a black background color to the top navigation */
nav {
  overflow: hidden;
}

/* Style the links inside the navigation bar */
nav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

/* Change the color of links on hover */
nav a:hover {
  background-color: black;
  color: #ddd;
}

section {
  width: 75%;
  display: inline-block;
  margin: 10px;
}

section h2 {
  text-decoration: underline;
  padding-top: 20px;
  padding-left: 20px;
}

.content {
  margin-bottom: 20px;
  height: 300px;
  background-color: #000000bd;
  color: #ffffff;
  border-radius: 5px;
  display: flex;
  justify-content: center;
}

.content h2 {
  margin-bottom: 20px;
  font-size: 36px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  /* justify-content: center; */
  /* align-items: center; */
}

.content p {
  padding: 40px;
  font-size: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

#about-me {
  /* border-radius: 5px; */
  height: fit-content;
}

#projects {
  display: flex;
  flex-wrap: wrap;
  height: max-content;
  border-radius: 5px;
  margin-bottom: 20px;;
}

.icon {
  align-self: center;
  height: 150px;
  width: 150px;
  padding: 10px;
  margin: 40px;
  text-align: center;
}

.icon img {
  height: 150px;
  width: 150px;
  border-style: groove;
  border-radius: 5px;
}

aside {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  margin-right: 10px;
  padding: 20px;
  float: left;
  width: 330px;
  height: fit-content;
  background-image: linear-gradient(to right, rgb(8, 142, 151) , rgb(5, 82, 170));
  border-radius: 5px;
}

aside h1 {
  text-align: center;
  text-decoration: underline;
}

aside img {
  display: block;
  margin: 10px auto;
  max-width: 150px;
  border-style: groove;
  border-radius: 5px;
}

.contact {
  margin-bottom: 32px;
  color: #ffffff;
}

.contact h3 {
  margin-bottom: 10px;
  text-align: center;
}